.input {
    width: 100%;
    padding-left: 2.5rem;
    padding-right: .5rem;
    background-color: #ECECF9;
    border: 1px solid #635D80;
    border-radius: 0.12rem;
    background: url('../../assets/images/search.png') no-repeat 3% 50%;
}

.input[placeholder] {
    font-size: 0.9rem;
    line-height: 2.25;
    color: #2D2E46;
    opacity: 0.5;
}